import React, { Component } from 'react'
import injectSheet from 'react-jss'
import { colors } from '../constants'
import { mediaWidth, gridColumnTemplate } from '../helpers'

const styles = {
  container: {
    width: '100%',
    display: 'grid',
    height: 'auto',
    ...gridColumnTemplate(),
    gridTemplateRows: '100px auto 10px auto',
    ...mediaWidth(600, {
      gridTemplateRows: '50px auto 10px auto',
    })
  },
  background: {
    gridColumn: '1 / -1',
    gridRow: '1 / -1',
  },
  textLeft: {
    gridColumn: '2 / -8',
    marginRight: 40, 
    ...mediaWidth(768,{
      gridColumn: '2 / -2',
    }),
  },
  textRight: {
    gridColumn: '8 / -2',
    marginLeft: 40,
    ...mediaWidth(768,{
      gridColumn: '2 / -2',
    }),
  },
  textContainer: {
    maxWidth: '100%',
    gridRow: '2 / 3',
    ...mediaWidth(768,{
      gridRow: '4 / -2',
    }),
    '& h2': {
      display: 'block',
      color: colors.white,
      fontSize: '1.2em',
      margin: '30px 0',
      ...mediaWidth(600,{
        fontSize: '1.6em',
      })
    },
    '& h2::after': {
      content: '""',
      display: 'block',
      position: 'absolute',
      width: 225,
      height: 5,
      background: colors.mainColor,
      marginTop: 15,
      ...mediaWidth(600,{
        height: 3,
        marginTop: 13
      })
    },
    '& p': {
      fontSize: '0.8em',
      color: colors.white,
      ...mediaWidth(600,{
        fontSize: '1.2em',
      })
    }
  },
  imgLeft: {
    gridColumn: '2 / -8',
    ...mediaWidth(768,{
      gridColumn: '2 / -2',
    }),
  },
  imgRight: {
    gridColumn: '8 / -2',
    ...mediaWidth(768,{
      gridColumn: '2 / -2',
    }),
  },
  image: {
    position: 'relative',
    border: `10px solid ${colors.white}`,
    marginTop: 26,
    maxWidth: 'calc(100% - 20px)',
    gridRow: '2 / 3'
  }
}

class CableInfoSection extends Component {
  render() {
    const { classes, cable, scrollRef } = this.props
    return (
      <div ref={scrollRef} className={classes.container}>
        <div className={classes.background}></div>
        <div
          className={`${classes.textContainer} ${cable.sort % 2 !== 0
            ? classes.textLeft : classes.textRight}`}
        >
          <h2>{cable.name}</h2>
          <p>{cable.info}</p>
        </div>
        <img
          ref={el => this.myRef = el}
          className={`${classes.image} ${cable.sort % 2 !== 0
            ? classes.imgRight : classes.imgLeft}`}
          src={cable.picture}
          alt={`Bertram Audio - ${cable.name}`}
        />
      </div>
    )
  }
}
export default injectSheet(styles)(CableInfoSection)