import React, { Component } from 'react'
import TopMenu from './topMenu'
import injectSheet from 'react-jss'
import { colors, cables } from '../constants'
import { mediaWidth, gridColumnTemplate } from '../helpers'

const styles = {
  container: {
    width: '100%',
    height: 'auto',
    display: 'grid',
    ...gridColumnTemplate(),
    gridTemplateRows: '70px 70px auto 350px',
    ...mediaWidth(768, {
      gridTemplateRows: '20px 40px auto auto',
    })
  },
  header: {
    gridColumn: '1 / -1',
    gridRow: '1 / -1',
    background: `radial-gradient(circle, rgba(17,21,22,0.3) 0%, rgba(17,21,22,0.9) 100%), linear-gradient(45deg, rgba(79,216,211,0.6) 0%, rgba(49,95,225,0.6) 100%), url(${require('../assets/img/bgLarge.png')})`,
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center top',
    backgroundSize: 'cover',
  },
  logo: {
    width: '100%',
    gridColumn: '2 / span 2',
    gridRow: '2 / 3',
    opacity: 0,
    position: 'relative',
    top: -100,
    transition: 'all ease-in-out 0.5s',
  },
  logoLoaded: {
    opacity: 1,
    top: 0,
  },
  menu: {
    gridColumn: 'span 6 / -2',
    gridRow: '2 / 3',
    opacity: 0,
    marginTop: -100,
    transition: 'all ease-in-out 0.5s',
    ...mediaWidth(768, {
      zIndex: 2,
    })
  },
  menuLoaded: {
    opacity: 1,
    marginTop: 0,
  },
  quote: {
    gridColumn: '3 / -3',
    gridRow: '3 / -2',
    textAlign: 'center',
    color: colors.white,
    fontSize: '1.4em',
    fontWeight: 500,
    margin: '20vh 0',
    transform: 'translateX(-20px)',
    opacity: 0,
    transition: 'all ease-in-out 0.5s',
    '& span': {
      color: colors.mainColor,
      fontFamily: 'raleway',
      fontWeight: 800
    }
  },
  quoteLoaded: {
    opacity: 1,
    transform: 'translateX(0)'
  },
  cablesBackground: {
    gridColumn: '1 / -1',
    gridRow: '4 / -1',
    background: colors.white,
    display: 'flex',
    transform: 'translateY(350px)',
    opacity: 0,
    transition: 'all ease-in-out 0.5s',
  },
  cableBgLoaded: {
    opacity: 1,
    transform: 'translateY(0)'
  },
  cables: {
    gridColumn: '2 / -2',
    gridRow: '4 / -1',
    display: 'flex',
    justifyContent: 'space-evenly',
    alignItems: 'center',
    ...mediaWidth(768, {
      flexDirection:'column'
    }),
    '& div:nth-child(1)': {
      transitionDelay: '0.4s',
      ...mediaWidth(768, {
        marginTop: 60
      })
    },
    '& div:nth-child(2)': {
      transitionDelay: '0.5s'
    },
    '& div:nth-child(3)': {
      transitionDelay: '0.6s',
      ...mediaWidth(768, {
        marginBottom: 60
      })
    }
  },
  cableContainer: {
    position: 'relative',
    width: 300,
    height: 200,
    margin: 20,
    borderRadius: 5,
    cursor: 'pointer',
    boxShadow: '0px 0px 15px 1px rgba(0,0,0,0.3)',
    transform: 'translateY(50px)',
    opacity: 0,
    transition: 'all ease-in-out 0.3s',
    ...mediaWidth(768, {
      width: 200,
      minHeight: 180,
    }),
    '& p': {
      backgroundColor: colors.black,
      color: colors.white,
      position: 'absolute',
      textAlign: 'center',
      width: 'calc(100% - 20px)',
      padding: 10,
      fontWeight: 600,
      fontSize: '1.0em',
      bottom: 0,
      borderRadius: '0px 0px 5px 5px',
      ...mediaWidth(768, {
        padding: 5,
        width: 'calc(100% - 10px)',
      }),
      ...mediaWidth(600, {
        fontSize: '1.4em',
      })
    }
  },
  cableLoaded: {
    opacity: 1,
    transform: 'translateY(0)'
  },
  logoSvg: {
    fill: colors.mainColor,
  }
}

class Cover extends Component {
  constructor (props) {
    super(props)
    this.state = {
      loaded: false
    }
    this.handleClick = this.handleClick.bind(this)
  }
  componentDidMount () {
    setTimeout(() => {
      this.setState({ loaded: true})
    }, 100);
  }
  handleClick (refName) {
    if(this.props.refs) {
      const ref = this.props.refs[refName]
      window.scrollTo({
        top: ref.current.offsetTop,
        left: 0,
        behavior: 'smooth'
      })
    }
  }
  render() {
    const { loaded } = this.state
    const { classes, refs } = this.props
    return (
      <div
        className={classes.container}
        ref={refs.home}
      >
        <div className={classes.header}></div>
        <div className={`${classes.logo} ${loaded ? classes.logoLoaded : null}`}><img height='100%' src={require('../assets/img/logo5.png')} alt='Bertram Audio Logo'/></div>
        <div className={`${classes.menu} ${loaded ? classes.menuLoaded : null}`}>
          <TopMenu refs={refs}/>
        </div>
        <p className={`${classes.quote} ${loaded ? classes.quoteLoaded: null}`}><span>High  quality</span> signal <span>cables</span> for your need.</p>
        <div className={`${classes.cablesBackground} ${loaded ? classes.cableBgLoaded: null}`}></div>
        <div className={classes.cables}>
          {cables.map((cable, idx) => {
            return(
              <div
                onClick={() => this.handleClick(`cable${idx}`)}
                key={cable.sort}
                className={
                  `${classes.cableContainer} ${loaded ? classes.cableLoaded: null}`
                }
                style={idx !== 2 ? {
                  background: `linear-gradient(45deg, rgba(79,216,211,0.4) 0%, rgba(49,95,225,0.4) 100%), url(${cable.picture})`,
                  backgroundSize: 'cover, cover',
                } : {
                  background: `url(${cable.picture})`,
                  backgroundPosition: '50% 75%',
                  backgroundSize: '100%',
                }}
              >
                <p>{cable.name}</p>
              </div>
            )
          })}
        </div>
      </div>
    )
  }
}
export default injectSheet(styles)(Cover)
