class TopMenu extends Component {
  constructor (props) {
    super(props)
    this.state = {
      menuToggle: false,
      cableToggle: false,
    }
    this.handleClick = this.handleClick.bind(this)
  }
  handleMenuToggle () {
    if(this.state.menuToggle) {
      this.setState({
        cableToggle: false
      })
    }
    this.setState({
      menuToggle: !this.state.menuToggle
    })
  }
  handleCableToggle () {
    this.setState({
      cableToggle: !this.state.cableToggle
    })
  }
  handleClick (refName) {
    this.setState({
      cableToggle: false,
      menuToggle: false
    })
    if(this.props.refs) {
      const ref = this.props.refs[refName]
      window.scrollTo({
        top: ref.current.offsetTop,
        left: 0,
        behavior: 'smooth'
      })
    }
  }
  render() {
    const { menuToggle, cableToggle } = this.state
    const { classes } = this.props
    return (
      <div
        className={`${classes.list} ${menuToggle ? classes.listActive : null}`}
      >
        <ul className={classes.listWrapper}>
          <li>
            <span onClick={this.handleCableToggle.bind(this)}>Cables</span>
            <ul className={classes.dropdown}>
              {cables && cables.map((item, idx) => {
                return (
                  <li
                    onClick={() => this.handleClick(`cable${idx}`)}
                    className={`${classes.cable} ${cableToggle ? classes.cableActive : null}`}
                    key={idx}
                  >
                    <span>{item.name}</span>
                  </li>
                )
              })
              }
            </ul>
          </li>
          <li
            onClick={() => this.handleClick('press')}
          >
            <span>Press</span>
          </li>
          <li
            onClick={() => this.handleClick('contactUs')}
          >
            <span>Contact</span>
          </li>
        </ul>
        <div className={`${classes.icon} ${menuToggle ? classes.menuActive : null}`} onClick={this.handleMenuToggle.bind(this)}>
          <div></div>
          <div></div>
          <div></div>
        </div>
      </div>
    )
  }
}
export default injectSheet(styles)(TopMenu)
