import React, { Component } from 'react'
import injectSheet from 'react-jss'
import { colors } from '../constants'
import { mediaWidth, gridColumnTemplate } from '../helpers'

const styles = {
  root: {
    width: '100%',
    display: 'grid',
    height: 'auto',
    ...gridColumnTemplate(),
    gridTemplateRows: '100px auto auto',
    backgroundColor: colors.white,
    ...mediaWidth(600, {
      gridTemplateRows: '50px auto auto auto',
    })
  },
  container: {
    display: 'flex',
    justifyContent: 'space-between',
    gridColumn: '2 / -2',
    gridRow: '2 / 3',
    ...mediaWidth(600, {
      display: 'block'
    })
  },
  logo: {
    display: 'block',
    height: '100%',
    ...mediaWidth(600, {
      width: 'auto',
      height: '100%',
      margin: 'auto',
      gridRow: '2 / 3',
      gridColumn: '2 / -2',
    })
  },
  infoContainer: {
    '& span': {
      display: 'block',
      textAlign: 'left',
      fontSize: '0.8em',
    },
    ...mediaWidth(600, {
      gridColumn: '2 / -2',
      gridRow: '3 / 4',
      margin: '50px 0px 0px 0px',
      '& span': {
        display: 'block',
        textAlign: 'center',
        fontSize: '1.2em',
      },
    })
  },
  copy:{
    margin: '100px 0px 20px 0px',
    textAlign: 'center',
    gridColumn: '2 / -2',
    gridRow: '3 / 4',
    fontSize: '0.8em',
    ...mediaWidth(600, {
      fontSize: '1.2em',
      gridColumn: '2 / -2',
      gridRow: '4 / 5',
      textAlign: 'center',
      margin: '20px 0px 20px 0px',
    })
  },
  logoSvg: {
    fill: colors.mainColor,
  }
}

class Footer extends Component {
  render() {
    const { classes } = this.props
    return (
      <div className={classes.root}>
        <div className={classes.container}>
          <img
            className={classes.logo}
            src={require('../assets/img/logo5blue.png')}
            alt='Footer Logo'
          />
          <div
          className={classes.infoContainer}
          >
            <span>Bertram Audio</span>
            <span>Some address 123</span>
            <span>city 45 678</span>
            <span>Sweden</span>

          </div>
         </div>
         <span className={classes.copy}>All content &copy; 2020 by Bertram Audio</span>
      </div>
    )
  }
}
export default injectSheet(styles)(Footer)
