import React, { Component } from 'react'
import injectSheet from 'react-jss'
import { colors } from '../constants'
import { pdf1, pdf2, pdf3, pdf4 } from '../assets/documents'
import { mediaWidth, gridColumnTemplate } from '../helpers'

const styles = {
    root: {
      width: '100%',
      display: 'grid',
      height: 'auto',
      ...gridColumnTemplate(),
      gridTemplateRows: 'auto auto auto',
      backgroundColor: colors.white,
    },
    header: {
      padding: '100px 0px 50px 0px',
      gridColumn: '2 / -2',
      gridRow: '1 / 2',
      fontSize: '1.2em',
      ...mediaWidth(600, {
        fontSize: '1.6em',
        padding: '50px 0px 25px 0px',
      })
    },
    map: {
      width: '100%',
      fill: colors.mainColor,
      gridColumn: '2 / -2',
      gridRow: '2 / 3',
    },
    text: {
      gridColumn: '2 / -2',
      gridRow: '3 / 4',
      paddingBottom: 100,
      fontSize: '0.8em',
      ...mediaWidth(600, {
        fontSize: '1.2em',
      }),
      '& a': {
        display: 'block',
        width: '100%',
        marginTop: 4,
        transition: 'all 0.3s ease',
        '&:link': {
          transition: 'all 0.3s ease',
          color: colors.mainColor
        },
        '&:visited': {
          transition: 'all 0.3s ease',
          color: colors.mainColor
        },
        '&:hover': {
          transition: 'all 0.3s ease',
          color: colors.black
        },
        '&:active': {
          transition: 'all 0.3s ease',
          color: colors.black
        },
      }
    }
}

class Press extends Component {
  render() {
    const { classes, scrollRef } = this.props
    return (
      <div
        className={classes.root}
        ref={scrollRef}
      >
        <h1 className={classes.header}>Press</h1>
        <p className={classes.text}>
          Bertram audio cables have gotten a lot of fine reviews over the years. We’ve included a few here, but please feel free to send us links and pictures etc. so that we can add it to the collection.
          <br/><br/>
          <a href={pdf1} rel="noopener noreferrer" target="_blank">Fidelity review 34 (Norwegian)</a>
          <a href={pdf2} rel="noopener noreferrer" target="_blank">Fidelity review 34 (English)</a>
          <a href={pdf3} rel="noopener noreferrer" target="_blank">Fidelity #5 2008 (Norwegian)</a>
          <a href={pdf4} rel="noopener noreferrer" target="_blank">Great review in HiFi4All (Danish)</a>
        </p>
      </div>
    )
  }
}
export default injectSheet(styles)(Press)
