import React, { Component } from 'react'
import injectSheet from 'react-jss'
import { colors } from '../constants'
import { mediaWidth } from '../helpers'
import Arrow from '../assets/svg/arrow'
import Facebook from '../assets/svg/facebook'
import Instagram from '../assets/svg/instagram'

const styles = {
  root: {
    position: 'fixed',
    display: 'block',
    width: 40,
    height: 40,
    padding: 10,
    bottom: 220,
    borderRadius: 50,
    right: 20,
    opacity: 0,
    cursor: 'default',
    transition: 'all ease 0.5s',
    border: `5px solid ${colors.mainColor}`,
    backgroundColor: colors.white,
    userSelect: 'none',
    ...mediaWidth(992, {
      width: 20,
      height: 20,
      bottom: 110,
      right: 10,
      border: `2px solid ${colors.mainColor}`,
    }),
    '&:hover': {
      transition: 'all ease 0.5s',
      backgroundColor: colors.mainColor,
      '& svg': {
        transition: 'all ease 0.5s',
        fill: colors.white
      }
    }
  },
  facebook: {
    bottom: 120,
    ...mediaWidth(992, {
      bottom: 60,
    }),
  },
  instagram: {
    bottom: 20,
    ...mediaWidth(992, {
      bottom: 10,
    }),
  },
  active: {
    opacity: 1,
    transition: 'all ease 0.5s',
    cursor: 'pointer',
  },
  icon: {
    transition: 'all ease 0.5s',
    width: 40,
    height: 40,
    fill: colors.mainColor,
    ...mediaWidth(992, {
      width: 20,
      height: 20,
    }),
  }
}

class ScrollToTop extends Component {
  constructor (props) {
    super(props)
    this.state = {
      scrolled: false
    }
  }
  handleClick () {
    console.log('hej')
    if(this.props.scrollRef) {
      const ref = this.props.scrollRef
      window.scrollTo({
        top: ref.current.offsetTop,
        left: 0,
        behavior: 'smooth'
      })
    }
  }
  componentDidMount () {
    window.addEventListener('scroll', this.handleScroll.bind(this));
  }

  componentWillUnmount () {
      window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll () {
    const winScroll =
    document.body.scrollTop || document.documentElement.scrollTop

    const height =
      document.documentElement.scrollHeight -
      document.documentElement.clientHeight

    const scrolled = (winScroll / height) > 0

    this.setState({
      scrolled
    })
  }
  render() {
    const { classes } = this.props
    const { scrolled } = this.state
    return (
      <React.Fragment>
        <div
          className={`${classes.root} ${classes.active} ${classes.facebook}`}
          onClick={this.handleClick.bind(this)}
        >
          <Facebook className={classes.icon}/>
        </div>
        <div
          className={`${classes.root} ${classes.active} ${classes.instagram}`}
          onClick={this.handleClick.bind(this)}
        >
          <Instagram className={classes.icon}/>
        </div>
        <div
          className={`${classes.root} ${scrolled ? classes.active : null}`}
          onClick={this.handleClick.bind(this)}
        >
          <Arrow className={classes.icon}/>
        </div>
      </React.Fragment>
    )
  }
}
export default injectSheet(styles)(ScrollToTop)
